import React, {useState} from 'react';
import {Button, TextField} from "@mui/material";
import s from "../../style.module.css";
import {createUserTC, signInTC} from "../../authReducer";
import {useDispatch, useSelector} from "react-redux";
import {Navigate} from "react-router-dom";

const Regist = () => {
    const [emailValue, setEmailValue] = useState('')
    const [passwordValue, setPasswordValue] = useState('')
    const [name, setName] = useState('')

    const dispatch = useDispatch()
    const setEmailAndPasswordToFirebase = () => dispatch(createUserTC(emailValue, passwordValue, name))

    const inAuth = useSelector((state) => state.authReducer.inAuth)
    if (inAuth) return <Navigate to={'/profile'}/>
    return (
        <div className={s.auth}>
            <div className={s.auth_wrapper}>
                <TextField label="Email" variant="standard" onChange={(e) => setEmailValue(e.currentTarget.value)}/>
                <TextField label="Password" variant="standard"
                           onChange={(e) => setPasswordValue(e.currentTarget.value)}/>
                <TextField label="Name" variant="standard"
                           onChange={(e) => setName(e.currentTarget.value)}/>
                <Button variant="outlined" onClick={setEmailAndPasswordToFirebase}>Зарегистрироваться</Button>
            </div>
        </div>
    );
};

export default Regist;
