import React, {useEffect, useRef, useState} from 'react';
import {Button, TextField} from "@mui/material";
import {useParams} from "react-router-dom";
import {onValue, ref} from "firebase/database";
import {database} from "../../api/initialFireBase";
import {useSelector} from "react-redux";
import {Howl, Howler} from 'howler';
import Sound from '../../sound/sound.mp3'

const ChatMessage = ({addedUserMessages}) => {
    const [value, setValue] = useState('')
    const [data, setData] = useState({})

    const {id} = useParams()
    const inputRef = useRef();

    const {name, uid} = useSelector((state) => state.authReducer)

    useEffect(() => {
        const starCountRef = ref(database, '/chats/' + id + '/messages/');
        onValue(starCountRef, (snapshot) => {
            const data = snapshot.val()

            if (data !== null) {
                setData({...data})
            } else {
                setData({})
            }
        })
    }, [id])

    useEffect(() => {
        const section = document.querySelector('#mess');
        section.scrollTo(10000, 10000);
    }, [data])

    useEffect(() => {
        const allData = Object.keys(data)?.sort((a, b) => {
            return data[a] - data[b]
        })

        if (data[allData.at(-1)]?.author === name) return

        let sound = new Howl({
            src: [Sound]
        });

        sound.play();

    }, [data, name])


    const addedMessage = (value) => {
        addedUserMessages(name , value, id)
        setValue('')
    }

    const MessagesItems = () => Object.keys(data)?.sort((a, b) => {
        return data[a] - data[b]
    }).map((key) => {

        return <div key={key}
                    className={`${'block_message'} ${data[key]?.author !== name ? 'block_message_user' : 'block_message_bot'}`}>
            <h4 className={'block_message_author'}>{data[key]?.author}</h4>
            <p className={'block_message_message'}>{data[key]?.message}</p>
        </div>
    })

    return (
        <div className="App">

            <div className={'owner_block_message'}>
                <div id={'mess'} className={'wrapper_block_message'}>
                    <MessagesItems/>
                </div>
                <form onSubmit={(e) => {
                    e.preventDefault()
                }} className={'form'}>

                    <TextField
                        id="outlined-multiline-flexible"
                        label="Write message"
                        multiline
                        maxRows={4}
                        value={value}
                        onChange={(e) => {
                            setValue(e.target.value)
                            e.preventDefault()
                        }}
                        inputRef={inputRef}
                        onKeyPress={(e) => {
                            if (e.key === 'Enter') {
                                addedMessage(value)
                            }
                        }}
                    />
                    <Button onClick={() => addedMessage(value)} variant="outlined" type={'submit'}>Send message</Button>

                </form>

            </div>
        </div>);
};

export default ChatMessage;
